.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

body {
  min-height: 100vh;
  background-position: center;
  background-size: cover;
}

#root {
  position: relative;
}

#background_image {
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -100;
  top: 0;
  left: 0;
  filter: blur(10px);
}