.mCard {
    display: flex;
    flex-direction: column;
    position: relative;
    max-width: 350px;
    height: 630px;
    margin: 10px;
    background-color: rgba(255, 255, 255, 0.9);
}

.mCard .icon {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    color: #fff700;
}

.mCard img {
    border-radius: 10px 10px 0px 0px;
    width: 100%;
    max-height: 530px;
}

.mCard .info {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.mCard .info .title {
    font-size: 22px;
    text-align: center;
}

.mCard .info .year {
    font-size: 25px;
    font-weight: bold;
}